import { useContext, useMemo } from 'react'
import { StyledCommuneInfosMessage } from './CommuneInfosMessage.style'
import ConfigContext from '../../../contexts/configContext'
import { MES_ADRESSES_URL } from '../../../hooks/useMesAdresses'
import { APIDepotRevision } from '../../../types/APIDepot.types'
import { isEmbeddedInIframe } from '../../../utils/iframe.utils'

type PublishedFromOtherClientProps = {
  currentRevision: APIDepotRevision
}

export const PublishedFromOtherClient = ({ currentRevision }: PublishedFromOtherClientProps) => {
  const { config } = useContext(ConfigContext)
  const client = currentRevision?.client

  const isOutdated = useMemo(() => {
    return (
      config?.communes?.outdatedApiDepotClients?.includes(currentRevision?.client?.id || '') ||
      false
    )
  }, [config, client])

  return (
    <StyledCommuneInfosMessage>
      <p>
        Une Base Adresse Locale a dj t publie par <b>{client.chefDeFile || client.nom}</b> pour
        votre commune.
      </p>
      {client.chefDeFileEmail && (
        <p>
          Vous pouvez contacter cet organisme via l&apos;adresse : <b>{client.chefDeFileEmail}</b>
        </p>
      )}
      {isOutdated ? (
        <p>
          Cependant cette organisation ne met plus  jour vos adresses. Afin de reprendre la main
          sur votre adressage, vous pouvez crer une Base Adresse Locale sur{' '}
          <a
            href={MES_ADRESSES_URL}
            className='fr-link'
            target={isEmbeddedInIframe() ? '_parent' : '_blank'}
            rel='noreferrer'
          >
            Mes-Adresses
          </a>
          et &quot;Forcer la publication&quot;.
        </p>
      ) : (
        <p>
          La commune reste toutefois lautorit comptente en matire dadressage, et vous pouvez
          dcider  tout moment de reprendre la main sur la publication de votre BAL via
          l&apos;outil{' '}
          <a
            href={MES_ADRESSES_URL}
            className='fr-link'
            target={isEmbeddedInIframe() ? '_parent' : '_blank'}
            rel='noreferrer'
          >
            Mes-Adresses
          </a>
          .
        </p>
      )}
    </StyledCommuneInfosMessage>
  )
}
