import React, { useEffect, useState } from 'react'
import { StyledParticulierToubleshooting } from './ParticulierToubleshooting.styles'
import Loader from '../../common/Loader/Loader'
import AdresseNotFoundInBAN from '../AdresseNotFoundInBAN/AdresseNotFoundInBAN'
import AdresseFoundInBAN from '../AdresseFoundInBAN/AdresseFoundInBAN'
import { SignalementMode, SignalementType } from '../../../types/signalement.types'
import { browseToMesSignalements, getSignalementMode } from '../../../utils/signalement.utils'
import { getSignalementCommuneStatus } from '../../../lib/api-signalement'
import { getCurrentRevision } from '../../../lib/api-depot'
import SearchInput from '../../common/SearchInput'
import { SearchItemType } from '../../common/SearchInput/SearchInput'

interface APIAdresseResult {
  nom: string
  code: string
  postcode: string
}

export const ParticulierTroubleshooting = () => {
  const [adresse, setAdresse] = useState<{
    municipality: { nom: string; code: string } | null
    street: { nom: string; code: string } | null
    number: { nom: string; code: string } | null
  }>({
    municipality: null,
    street: null,
    number: null,
  })
  const [signalementMode, setSignalementMode] = useState<SignalementMode>(SignalementMode.EMAIL)
  const [numeros, setNumeros] = useState<{ nom: string; code: string }[]>([])
  const [numeroNotFound, setNumeroNotFound] = useState(false)
  const [isLoading, setIsLoading] = useState(false)

  const fetchBAN = (type: string) => async (search: string) => {
    if (!search || search.length < 3) {
      return []
    }

    setIsLoading(true)
    try {
      const response = await fetch(
        `https://data.geopf.fr/geocodage/search/?q=${search}&index=address&type=${type}&limit=10${
          adresse.municipality ? `&citycode=${adresse.municipality.code}` : ''
        }`,
      )
      const data = await response.json()

      return data.features.map(
        ({ properties }: { properties: { id: string; name: string; postcode?: string } }) => ({
          code: properties.id,
          nom: properties.name,
          label: properties.name,
          id: properties.id,
          ...(properties.postcode && { postcode: properties.postcode }),
        }),
      ) as SearchItemType<APIAdresseResult>[]
    } catch (error) {
      console.error(error)
      return []
    } finally {
      setIsLoading(false)
    }
  }

  const fetchNumeros = async () => {
    setIsLoading(true)
    try {
      const response = await fetch(
        `https://plateforme.adresse.data.gouv.fr/lookup/${adresse.street?.code}`,
      )
      const data = await response.json()
      const numeros: { code: string; nom: string }[] =
        data?.numeros.map(
          ({ numero, suffixe, id }: { numero: number; suffixe: string; id: string }) => ({
            code: id,
            nom: suffixe ? `${numero} ${suffixe}` : numero.toString(),
          }),
        ) || []

      return numeros
    } catch (error) {
      console.error(error)
      return []
    } finally {
      setIsLoading(false)
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      if (adresse.municipality && adresse.street) {
        try {
          const numeros = await fetchNumeros()
          setNumeros(numeros)
        } catch (err) {
          console.error(`Error fetching numbers for street ${adresse.street}`)
        }
      } else if (adresse.municipality) {
        let isCommuneDisabled: boolean
        try {
          const communeStatus = await getSignalementCommuneStatus(adresse.municipality.code)
          isCommuneDisabled = communeStatus.disabled
        } catch (err) {
          console.error('Error fetching commune disabled status:', err)
          setSignalementMode(SignalementMode.EMAIL)
          return
        }

        let currentRevision = null
        try {
          currentRevision = await getCurrentRevision(adresse.municipality.code)
        } catch (err) {
          console.error('Error fetching current revision:', err)
        }
        setSignalementMode(getSignalementMode(currentRevision, isCommuneDisabled))
      }
    }

    void fetchData()
  }, [adresse])

  return (
    <StyledParticulierToubleshooting>
      {adresse.municipality && adresse.street && adresse.number ? (
        <AdresseFoundInBAN adresseId={adresse.number.code} />
      ) : numeroNotFound ? (
        <AdresseNotFoundInBAN adresse={adresse} />
      ) : (
        <>
          <h2>Votre adresse existe-t-elle dans la Base d&apos;Adresse Nationale ?</h2>
          <div className='step'>
            <div className='step-label'>
              <span>1.</span>Dans quelle commune se situe votre adresse?
            </div>
            {adresse.municipality ? (
              <div className='selection'>
                Ma commune : <span>{adresse.municipality.nom}</span>{' '}
                <button
                  type='button'
                  onClick={() =>
                    setAdresse({
                      municipality: null,
                      street: null,
                      number: null,
                    })
                  }
                  className='fr-icon-close-line'
                  title='Rinitialiser la commune'
                />
              </div>
            ) : (
              <SearchInput
                label='Rechercher ma commune'
                onSearch={fetchBAN('municipality')}
                onSelect={(commune?: SearchItemType<APIAdresseResult> | null) => {
                  if (commune) {
                    setAdresse(() => ({ ...adresse, municipality: commune }))
                  }
                }}
                nativeInputProps={{ placeholder: 'Tours...', required: true }}
                itemToString={(commune?: SearchItemType<APIAdresseResult> | null) =>
                  commune ? `${commune.nom} (${commune.postcode})` : ''
                }
              />
            )}
          </div>
          {adresse.municipality && (
            <div className='step'>
              <div className='step-label'>
                <span>2.</span> Recherchez votre voie
              </div>
              {adresse.street ? (
                <div className='selection'>
                  Ma voie : <span>{adresse.street.nom}</span>{' '}
                  <button
                    type='button'
                    onClick={() =>
                      setAdresse((adresse) => ({
                        ...adresse,
                        street: null,
                        number: null,
                      }))
                    }
                    className='fr-icon-close-line'
                    title='Rinitialiser la voie'
                  />
                </div>
              ) : (
                <>
                  <SearchInput
                    label='Rechercher ma voie'
                    nativeInputProps={{ placeholder: 'Rue Nationale...', required: true }}
                    onSearch={fetchBAN('street')}
                    onSelect={(voie?: SearchItemType<APIAdresseResult> | null) => {
                      if (voie) {
                        setAdresse(() => ({
                          ...adresse,
                          street: { nom: voie.nom, code: voie.code },
                        }))
                      }
                    }}
                  />
                  <button
                    type='button'
                    className='fr-btn fr-btn--secondary fr-btn--sm not-found'
                    {...(signalementMode === SignalementMode.MES_SIGNALEMENTS
                      ? {
                          onClick: () => {
                            if (adresse.municipality?.code) {
                              browseToMesSignalements(
                                adresse.municipality.code,
                                SignalementType.LOCATION_TO_CREATE,
                              )
                            }
                          },
                        }
                      : {
                          onClick: () => setNumeroNotFound(true),
                        })}
                  >
                    Je ne trouve pas ma voie
                  </button>
                </>
              )}
            </div>
          )}
          {adresse.municipality && adresse.street && (
            <div className='step'>
              <div className='step-label'>
                <span>3.</span> Slectionnez votre numro
              </div>

              {isLoading ? (
                <Loader />
              ) : (
                <>
                  <select
                    defaultValue=''
                    className='fr-select'
                    onChange={(event) =>
                      setAdresse(() => ({
                        ...adresse,
                        number: { nom: event.target.value, code: event.target.value },
                      }))
                    }
                  >
                    <option value='' disabled>
                      -- Choisir votre numro dans la liste
                    </option>
                    {numeros.map(({ code, nom }) => (
                      <option key={code} value={code}>
                        {nom}
                      </option>
                    ))}
                  </select>
                  <button
                    type='button'
                    className='fr-btn fr-btn--secondary fr-btn--sm not-found'
                    {...(signalementMode === SignalementMode.MES_SIGNALEMENTS
                      ? {
                          onClick: () => {
                            if (adresse.street?.code) {
                              browseToMesSignalements(
                                adresse.street.code,
                                SignalementType.LOCATION_TO_CREATE,
                              )
                            }
                          },
                        }
                      : {
                          onClick: () => setNumeroNotFound(true),
                        })}
                  >
                    Mon numro n&apos;est pas dans la liste
                  </button>
                </>
              )}
            </div>
          )}
        </>
      )}
    </StyledParticulierToubleshooting>
  )
}
