import { StyledBALStatusBadge } from './BALStatusBadge.styles'
import { BALMesAdresses } from '../../../types/MesAdresses.types'
import { Tooltip } from '@codegouvfr/react-dsfr/Tooltip'

interface BALStatusBadgeProps {
  status: 'published' | 'replaced'
  sync: {
    status: 'conflict' | 'paused' | 'outdated' | 'synced'
    isPaused: boolean
  }
  balId: string
}

const STATUSES = {
  conflict: {
    label: 'Conflit',
    content:
      'Une autre Base Adresses Locale est aussi synchronise avec la Base Adresse Nationale. Veuillez entrer en contact les administrateurs de lautre Base Adresse Locale ou notre support: adresse@data.gouv.fr',
    color: '#732e2b',
    background: 'rgb(244, 219, 219)',
  },
  paused: {
    label: 'Suspendue',
    content:
      'Les mises  jour automatiques de cette Base Adresse Locale sont actuellement suspendues. Vous pouvez relancer la synchronisation  tout moment.',
    color: '#61471a',
    background: 'rgb(252, 240, 213)',
  },
  outdated: {
    label: 'Mise  jour programme',
    content:
      'De nouvelles modifications ont t dtectes, elles seront automatiquement rpercutes dans la Base Adresse Nationale dans les prochaines heures.',
    color: '#3351c5',
    background: 'rgb(216, 224, 242)',
  },
  synced: {
    label: ' jour',
    content:
      'Cette Base Adresse Locale est  jour avec la Base Adresse Nationale. Toute modification sera automatiquement rpercute dans la Base Adresse Nationale dans les prochaines heures.',
    color: '#42705b',
    background: 'rgb(224, 241, 234)',
  },
}

export function computeStatus(balStatus: BALMesAdresses['status'], sync: BALMesAdresses['sync']) {
  if (balStatus === 'replaced' || sync?.status === 'conflict') {
    return STATUSES.conflict
  }

  if (sync?.isPaused) {
    return STATUSES.paused
  }

  if (balStatus === 'published') {
    return STATUSES[sync.status]
  }

  return STATUSES[balStatus]
}

export const BALStatusBadge = ({ status, sync, balId }: BALStatusBadgeProps) => {
  const computedStatus = computeStatus(status, sync)
  return (
    <Tooltip kind='hover' title={computedStatus.content} id={`bal-status-${balId}`}>
      <StyledBALStatusBadge
        iconId='fr-icon-info-line'
        iconPosition='right'
        priority='tertiary no outline'
        title='En savoir plus sur le statut de cette Base Adresse Locale'
        aria-describedby={`bal-status-${balId}`}
        $color={computedStatus.color}
        $background={computedStatus.background}
        size='small'
      >
        <span className='bal-status-badge__label'>{computedStatus.label}</span>
      </StyledBALStatusBadge>
    </Tooltip>
  )
}
