import { AdresseProblemFormType } from './AdresseProblemForm'

export const mailToSignalement = (
  bodyData: AdresseProblemFormType,
  publication: { client?: string; balId?: string; organization?: string },
) => `Bonjour,

Je vous contacte car mon adresse n'est pas rfrence dans la Base Adresse Nationale. Voici le dtail de mon signalement, mes coordonnes ainsi que la procdure  suivre afin de prendre en compte le signalement :

${bodyData.city ? `Commune : ${bodyData.cityName}` : ''}
${bodyData.street ? `Voie : ${bodyData.street}` : ''}
${bodyData.number ? `Numro : ${bodyData.number}` : ''}
${bodyData.message ? `Message : ${bodyData.message}` : ''}

${
  bodyData.firstName || bodyData.lastName
    ? `Coordonnes de contact : ${bodyData.firstName} ${bodyData.lastName}`
    : ''
}

${
  publication.client === 'Mes Adresses'
    ? `Afin de prendre en compte ce signalement, vous pouvez vous rendre sur le site http://mes-adresses.data.gouv.fr sur la page de la Base Adresse Locale de votre commune (https://mes-adresses.data.gouv.fr/bal/${publication.balId}).`
    : publication.client === 'Moissonneur BAL'
      ? `Afin de prendre en compte ce signalement, vous pouvez vous rapprocher de l'organisation ${publication.organization} qui gre la publication de la Base Adresse Locale de votre commune.`
      : publication.client
        ? `Afin de prendre en compte ce signalement, vous pouvez vous rapprocher de l'organisation ${publication.client} qui gre la publication de la Base Adresse Locale de votre commune.`
        : `Vous pouvez prendre en compte ce signalement en vous rendant sur le site http://mes-adresses.data.gouv.fr et en crant une Base Adresse Locale pour votre commune. Pour vous aider dans cette dmarche, vous pouvez consulter cet article (https://guide.mes-adresses.data.gouv.fr/creeer-une-base-adresse-locale/creer-une-nouvelle-base-adresse-locale) qui explique les tapes de la cration d'une Base Adresse Locale. Vous trouverez aussi de la documentation et des tutoriels vidos directement sur le site https://mes-adresses.data.gouv.fr. Enfin, vous pouvez vous inscrire pour suivre un de nos webinaires sur la prise en main de l'outil sur cette page : https://adresse.data.gouv.fr/formation-en-ligne.`
}

Vous pouvez prendre contact avec l'quipe d'adresse.data.gouv.fr  cette adresse : adresse@data.gouv.fr

Cordialement,
`
